
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  font-size: 24px;
  color: #00a0ff;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
  padding: 6px;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  transform: translate3D(20px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
  vertical-align: top;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  color: #00a0ff;
}


